import React from 'react';
import type { FC } from 'react';
import { match } from 'variant';
import { Box, Text, Alert, Spinner, useAsync } from '@wix/cli-ui-kit';
import { useARMClient } from '@wix/arm-client';
import { useProjectModel } from '../../model.js';

interface ReleaseProps {
  previewId: string;
}

export const ReleaseOnlyTag: FC<ReleaseProps> = ({ previewId }) => {
  const {
    model: {
      config: { appId },
    },
  } = useProjectModel();

  const { release } = useARMClient();

  const { status } = useAsync(async () => {
    const baseUrl = await release(appId, previewId);

    return {
      baseUrl,
    };
  }, []);

  return (
    <>
      {match(status, {
        NotRequested: () => null,
        Error: () => null,
        Loading: () => <Spinner text={<Text>Releasing...</Text>} />,
        Success: ({ result: { baseUrl } }) => (
          <Box flexDirection="column" gap={1}>
            <Alert type="success">
              <Text bold>Site published on {baseUrl}</Text>
            </Alert>
          </Box>
        ),
      })}
    </>
  );
};
